"use client";

import { useState } from "react";
import { AnimatePresence } from "framer-motion";
import { useRouter } from "next/navigation";

import LeftSideLayout from "@/components/common/LeftSideLayout";
import Phone from "@/components/auth/Phone";


export default function GetStartedPage() {
  const router = useRouter();
  const [phone, setPhone] = useState("");
  const [agreed1, setAgreed1] = useState(true);
  const [agreed2, setAgreed2] = useState(true);
  const [showOTP, setShowOTP] = useState(false);
  const [otp, setOtp] = useState(["", "", "", ""]);

  const handleOtpChange = (index: number, value: string) => {
    if (value.length > 1) return;
    const newOtp = [...otp];
    newOtp[index] = value.replace(/[^0-9]/g, "");
    setOtp(newOtp);
  };

  return (
    <div className="h-screen w-screen overflow-hidden flex flex-col bg-[var(--color-bg-neutral-50)]">
      {/* ✅ NAVBAR */}
      <header className="w-full bg-[var(--color-bg-neutral-50)] border-b border-[var(--color-btn-teritary-200)] z-50 py-4 px-28">
        <div className="flex items-center space-x-4">
          <img src="/logo.png" alt="Workly Logo" className="h-6 w-auto" />
        </div>
      </header>

      {/* ✅ MAIN SECTION */}
      <main className="flex flex-1 h-full m-0 p-0">
        {/* ✅ LEFT SIDE (Reusable Common Layout) */}
        <LeftSideLayout />

        {/* ✅ RIGHT SIDE */}
        <div className="w-1/2 h-full flex items-center justify-center bg-[var(--color-btn-teritary-100)]">
          <AnimatePresence mode="wait">
            {!showOTP ? (
              <Phone
                phone={phone}
                setPhone={setPhone}
                agreed1={agreed1}
                setAgreed1={setAgreed1}
                agreed2={agreed2}
                setAgreed2={setAgreed2}
                onSubmit={() => setShowOTP(true)}
              />
            ) : (
              <EnterVerificationCode
                phone={phone}
                otp={otp}
                handleOtpChange={handleOtpChange}
                onResendOtp={() => alert("OTP Resent")}
                onVerify={() => router.push("/getstarted/details")}
              />
            )}
          </AnimatePresence>
        </div>
      </main>
    </div>
  );
}
